.day2 {
	display: flex;
	gap: 20rem;
}

.frame2 {
    position: absolute;
    top: 55%;
    left: 70%;
    width: 400px;
    height: 400px;
    margin-top: -200px;
    margin-left: -200px;
    border-radius: 12px;
    box-shadow: 4px 8px 16px 0 rgba(0,0,0,0.1);
    overflow: hidden;
    background: #3FAF82;
    font-family: 'Open Sans', Helvetica, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  
  .hamburger {
      display: grid;
      justify-content: center;
      align-items: center;
      width: 90px;
      height: 60px;
  }
  
  .line {
      position: absolute;
      width: 90px;
      height: 8px;
      background-color: white;
      border-radius: 4px;
      box-shadow: 0px 0px 10px #777;
  }
  
  .hamburger.close .line:nth-child(1) {
      animation: close1 0.8s ease-in-out forwards;
  }
  
  .hamburger.close .line:nth-child(2) {
      animation: close2 0.8s ease-in-out forwards;
  }
  
  .hamburger.close .line:nth-child(3) {
      animation: close3 0.8s ease-in-out forwards;
  }
  
  @keyframes close1 {
      0% {
          transform: translatey(-20px);
      }
      50% {
          transform: translatey(0px);
      }
      100% {
          transform: rotate(45deg);
      }
  }
  
  @keyframes close2 {
      0% {
          transform: scale(100%);
      }
      50% {
          transform: scale(50%);
      }
      100% {
          transform: scale(0%);
      }
  }
  
  @keyframes close3 {
      0% {
          transform: translatey(20px);
      }
      50% {
          transform: translatey(0);
      }
      100% {
          transform: rotate(135deg);
      }
  }
  
  .line:nth-child(1) {
      animation: animation1 0.8s ease-in-out forwards;
  }
  
  .line:nth-child(2) {
      animation: animation2 0.8s ease-in-out forwards;
  }
  
  .line:nth-child(3) {
      animation: animation3 0.8s ease-in-out forwards;
  }

  .day-info {
	width: 600px;
	margin-top: 20px;
	margin-left: 4px;
	font-size: calc(15px + 2vmin);
	font-family: 'Raleway';
	font-weight: 700;
}
  
.day-number {
	color: #7b61ff;
}

.text-info {
	font-size: calc(4px + 2vmin);
	font-family: 'Raleway';
	font-weight: 500;
}

.challenge-name {
	font-weight: 700;
}
  
  @keyframes animation1 {
      0% {
          transform: rotate(45deg);
      }
      50% {
          transform: translatey(0px) rotate(0deg);
      }
      100% {
          transform: translatey(-20px);
      }
  }
  
  @keyframes animation2 {
      0% {
          transform: scale(0%);
      }
      50% {
          transform: scale(50%);
      }
      100% {
          transform: scale(100%);
      }
  }
  
  @keyframes animation3 {
      0% {
          transform: rotate(135deg);
      }
      50% {
          transform: translatey(0px) rotate(0deg);
      }
      100% {
          transform: translatey(20px);
      }
  }